<template>
	<view>
		<view class="uni-timeline" style="padding: 30upx 20upx;background-color: #fff;">
			<view v-for="(item,index) in list" :key="index" class="uni-timeline-item" :class="{'uni-timeline-first-item':index === 0,'uni-timeline-last-item' :index === list.length - 1}">
				<view class="uni-timeline-item-divider"></view>
				<view class="uni-timeline-item-content">
					<view>
						{{item.status}}
					</view>
					<view class="datetime">
						{{item.time}}
					</view>
				</view>
			</view>
			<!-- 没有数据时 -->
			<view v-if="list.length === 0" class="d-flex a-center j-center py-5">
				<text class="font-md text-light-muted">暂无物流信息</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id:0,
				list:[]
			}
		},
		onLoad(e) {
			if(!e.detail){
				return uni.showToast({
					title:'非法参数',
					icon:'none'
				})
			}
			this.id = (JSON.parse(e.detail)).id
			this.getShipInfo()
		},
		methods: {
			getShipInfo(){
				let obj = {
					1:'在途中',
					2:'派件中',
					3:'已签收',
					4:'派送失败(拒签等)'
				}
				
				this.$H.get('/order/'+this.id+'/get_ship_info',{},{
					token:true
				}).then(res =>{
					let status = obj[res.result.deliverystatus]
					if(status){
						uni.setNavigationBarTitle({
							title:status
						})
					}
					this.list = res.result
				}).catch(err =>{
					console.log('获取失败')
				})
			}
		}
	}
</script>

